import React, { useState } from 'react';
import Introduction from './components/introduction'
import ExamplesComponent from './components/examples'
import Activities from './components/activities'
import Assessment from './components/assessment';
import Logo from './assets/images/logo.PNG'
import IntroductionIcon from './assets/images/introduction.svg';
import ExampleIcon from './assets/images/example.svg';
import ActivityIcon from './assets/images/activity.svg';
import AssesmentIcon from './assets/images/assessment.svg'
import Note from './assets/images/note.svg';
import BookMark from './assets/images/bookmark.svg';
import './App.css';

const INTRODUCTION = 'Introduction';
const EXAMPLES = 'Examples';
const ACTIVITIES = 'Activities';
const ASSESSMENT = 'assessment'

function App() {
  const [tabName, setTabName] = useState(INTRODUCTION);
  const [showNotes, setShowNotes] = useState(false);

  return (
    <div className="App">
      <div className="container-fluid">
        <div className="row fill-height">
          <div className="col-md-2 left-panel fill-height">
            <div className="logo"><img src={Logo} alt={'App logo'} /></div>
            <div className="tabs-section">
              <div className="tab">
                <button
                  className={tabName === INTRODUCTION ? "tablinks active" : "tablinks"}
                  onClick={() => setTabName(INTRODUCTION)}
                  id="defaultOpen"
                >
                  <img src={IntroductionIcon} alt={''} className="tab-icons" /> &nbsp;{INTRODUCTION}
                </button>
                <button className={tabName === EXAMPLES ? "tablinks active" : "tablinks"} onClick={() => setTabName(EXAMPLES)}>
                  <img src={ExampleIcon} alt={''} className="tab-icons" /> &nbsp;{EXAMPLES}
                </button>
                <button className={tabName === ACTIVITIES ? "tablinks active" : "tablinks"} onClick={() => setTabName(ACTIVITIES)}>
                  <img src={ActivityIcon} alt={''} className="tab-icons" /> &nbsp;{ACTIVITIES}
                </button>
                <button className={tabName === ASSESSMENT ? "tablinks active" : "tablinks"} onClick={() => setTabName(ASSESSMENT)}>
                  <img src={AssesmentIcon} alt={''} className="tab-icons" /> &nbsp;{ASSESSMENT}
                </button>
              </div>
            </div>
          </div>

          <div className="col-md-10 right-panel fill-height">
            <div className="header">
              <div className="float-right">
                <span ><img onClick={() => setShowNotes(!showNotes)} src={Note} alt="Note" title="Note" /></span>
                <span ><img src={BookMark} alt="BookMark" title="BookMark" /></span>
              </div>
            </div>
            {tabName === INTRODUCTION && <Introduction />}
            {tabName === EXAMPLES && <ExamplesComponent />}
            {tabName === ACTIVITIES && <Activities />}
            {tabName === ASSESSMENT && <Assessment/>}
            <div className={showNotes ? 'notes' : 'notes hide-note'}>
              <div className="notes-header">
                <h3>Notes</h3>
              </div>
              <div className="note row">
                <p>Page Introduction<br />Today at 5:30 PM </p>
                <div className="col-md-12 note-content">The Doppler effect is of
                intense interest to astronomers who use the information
                about the shift in frequency of electromagnetic waves
                produced by moving stars in our galaxy and beyond in order
                      to derive information about those stars and galaxies.</div>
                <p className="add-note">Add Note..</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
